
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import axios from '@/api/axiosApi'
import { mapGetters, mapMutations } from 'vuex'
import { localUser } from '@/assets/js/local'
import { isIOS, isIphoneX } from '@/assets/js/brower'
import imageTop from '@/assets/img/default_avatar.png'
import dingImage from '@/assets/img/ding.png'
import Previewer from '@/components/previewer/previewer'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import answerImage from '@/assets/img/answer.png'
export default {
data () {
return {
imageTop: imageTop,
dingImage: dingImage,
answerImage: answerImage,
showHeaderBackground: false,
headUrl: 'http://p0w4yb0aj.bkt.clouddn.com/background_my.jpg',
followStatus: 0,
listTab: [],
starNum: 5,
index: 0,
swiperHeight: '500px',
scrollHeight: '',
opacity: 0,
user: {},
ifMe: true,
taDynamic: [],
selectAtlas: [],
travelList: [],
strategyList: [],
dynamicImgUrl: 'http://p0w4yb0aj.bkt.clouddn.com/background_my.jpg',
travelImgUrl: 'http://p0w4yb0aj.bkt.clouddn.com/background_my.jpg',
categaryImgUrl: 'http://p0w4yb0aj.bkt.clouddn.com/background_my.jpg'
}
},
components: {
VHeader, Scroll, isIOS, isIphoneX, localUser, axios, Tab, TabItem, Timeline, TimelineItem, Cell, Rater, Swiper, SwiperItem, Previewer
},
mounted () {

this.swiperHeight = window.innerHeight - 44 - 40 + 'px'
setTimeout(() => {
this.$refs.tabScroll.disable()
this.$refs.wrapperScroll.enable()
}, 200)
},
created () {

this.pageNum = 1
this.pageSize = 10
this.user = localUser.get() || {}
this.user.headUrl = sessionStorage.getItem('headUrl')

if (this.user.id === this.$route.params.userId) {
this.ifMe = false
}
this.paddingTop = 40
if (isIphoneX()) {
this.paddingTop += 40
} else {
this.paddingTop += 26
}

this.getTaDynamic()

this.handleGetTravel(0, this.user.id)

this.handleGetStrategy(1, this.user.id)

this.hangdleGetDynamicNum(this.user.id)

this.hangleGetTravelNum(this.user.id, 0)

this.hangleGetStrategyNum(this.user.id, 1)
this.listTab = [
'动态',
'游记',
'攻略'
]
},
computed: {
...mapGetters({
selected: 'archivesTab'
})
},
methods: {
...mapMutations({
setArchivesTab: 'SET_ARCHIVES_TAB'
}),
scrollWrapper (position, directionY) {
if (position.y <= -206) {
this.opacity = 1
} else {
this.opacity = Math.abs(position.y / 206)
}
if (position.y <= -190) {
if (this.$refs.wrapperScroll.scroll.enabled) {
this.$refs.wrapperScroll.scrollTo(0, -190)        
this.$refs.wrapperScroll.stopScroll()             
this.$refs.wrapperScroll.disable()                
}
this.$refs.tabScroll.enable()
this.$refs.tabThreeScroll.enable()
this.$refs.tabTravelScroll.enable()
} else {
if (this.$refs.tabScroll.scroll.enabled) {
this.$refs.tabScroll.stopScroll()
this.$refs.tabScroll.disable()
this.$refs.tabThreeScroll.stopScroll()
this.$refs.tabThreeScroll.disable()
this.$refs.tabTravelScroll.stopScroll()
this.$refs.tabTravelScroll.disable()
}
this.$refs.wrapperScroll.enable()
}
},
scrollTab (position, directionY) {
if (position.y >= 0) {
this.$refs.tabScroll.disable()
this.$refs.wrapperScroll.enable()
} else {
this.$refs.wrapperScroll.disable()
this.$refs.tabScroll.enable()
}
},
scrollTabTwo (position, directionY) {
if (position.y >= 0) {
this.$refs.wrapperScroll.enable()
} else {
this.$refs.wrapperScroll.disable()
}
},
scrollTabThree (position, directionY) {
if (position.y >= 0) {
this.$refs.tabThreeScroll.disable()
this.$refs.wrapperScroll.enable()
} else {
this.$refs.wrapperScroll.disable()
this.$refs.tabThreeScroll.enable()
}
},
scrollTravelTab (position, directionY) {
if (position.y >= 0) {
this.$refs.tabTravelScroll.disable()
this.$refs.wrapperScroll.enable()
} else {
this.$refs.wrapperScroll.disable()
this.$refs.tabTravelScroll.enable()
}
},
changeFollowStatus (targetUserId) {
const userId = localUser.get('id')
if (userId == null) {
this.$vux.toast.text('请先登录！', 'middle')
return
}
if (this.sendRequest) {
return
}
if (userId === targetUserId) {
this.$vux.toast.text('自己不能关注自己！', 'middle')
return
}
const valid = this.followStatus === 0 ? 1 : 0
this.followStatus = valid
this.sendRequest = true
const _self = this
axios.post(this, `/v1/follow/${userId}/${targetUserId}/${valid}`, null, (data) => {
_self.sendRequest = false
}, null, false)
},
sendMessage () {
if (localUser.get('id') == null) {
this.$vux.toast.text('请先登录！', 'middle')
return
}
this.$store.dispatch('setChatObject', this.user)
this.$router.push('/chat')
},
toFollow () {
if (localUser.get('id') == null) {
this.$vux.toast.text('请先登录！', 'middle')
return
}
this.$router.push('/user/focus/' + this.$route.params.userId)
},
toFans () {
if (localUser.get('id') == null) {
this.$vux.toast.text('请先登录！', 'middle')
return
}
this.$router.push('/user/fans/' + this.$route.params.userId)
},
selectTabEvery (value) {
this.setArchivesTab(value)
},
handleGoEvalutionDesc () {
this.$router.push('/user/evalutionDesc')
},
handleGoQuestionAndAnswer () {
this.$router.push('/user/questionandanswer')
},
myFocus () {
this.$router.push('/user/focus/' + this.user.id)
},
myFans () {
this.$router.push('/user/fans/' + this.user.id)
},

getTaDynamic () {
axios.get(this, '/v1/dynamic', {publisher: this.user.id, pageNum: this.pageNum, pageSize: this.pageSize}, (data) => {
if (data.length === 0) {
this.$vux.toast.text('没有更多数据了', 'middle')
if (this.$refs.tabScroll && this.pageNum > 1) {
this.$refs.tabScroll.hiddenPullUpLoad()
} else {
this.$refs.tabScroll.hiddenPullDown()
}
} else {
if (this.pageNum > 1) {
this.taDynamic = this.taDynamic.concat(data)
} else {
this.taDynamic = data
}
}
})
},

handleGetTravel (articleType, userId) {
axios.get(this, '/v1/playStrategy', {articleType: articleType, publishUserId: userId}, (data) => {
this.travelList = data
})
},
handleGetStrategy (articleType, userId) {
axios.get(this, '/v1/playStrategy', {articleType: articleType, publishUserId: userId}, (data) => {
this.strategyList = data
})
},

hangdleGetDynamicNum (userId) {
axios.get(this, '/v1/dynamicCount', {publisher: userId}, (data) => {
this.listTab[0] = '动态 ' + data
})
},

hangleGetTravelNum (userId, articleType) {
axios.get(this, '/v1/playStrategyCount', {publishUserId: userId, articleType: articleType}, (data) => {
this.listTab[1] = '游记 ' + data
})
},
hangleGetStrategyNum (userId, articleType) {
axios.get(this, '/v1/playStrategyCount', {publishUserId: userId, articleType: articleType}, (data) => {
this.listTab[2] = '攻略 ' + data
})
},

onPullingUp () {
this.pageNum++
this.getTaDynamic()
},
onPullingDown () {
this.pageNum = 1
this.getTaDynamic()
},

clickImg (imgs, index) {
this.$vux.loading.show({
text: '加载中...',
position: 'absolute'
})
let temp = []
imgs.forEach((img, i) => {
temp.push({
src: img
})
})
this.selectAtlas = temp
this.$nextTick(() => {
this.$refs.previewer.show(index)
})
},
preImgLoaded () {
this.$vux.loading.hide()
},
handleDynamic (dynamicId) {
this.$router.push('/meiTu/dynamicDesc/' + dynamicId)
},
handlePublishDynamic () {
this.$router.push('/meiTu/publishDynamic')
},
handlePublishTravel () {
this.$router.push('/address/travelsEdit/0')
},
handlePublishCategary () {
this.$router.push('/address/travelsEdit/1')
}
},
watch: {

}
}



